.open-modal {
    opacity: 1;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(50%, -50%) scale(1);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.body__open-modal {
    overflow: hidden;
}

// .body__close-modal {
//   overflow: auto;
// }
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
}

.fadeIn {
    animation-name: fadeIn;
    opacity: 1;
    transform: scale(1);
    visibility: visible;
    animation-duration: 0.5s;
    animation-timing-function: linear;
}

.fadeOut {
    animation-name: fadeOut;
    opacity: 0;
    transform: translate(750px, -250px) scale(0.6);
    animation-duration: 0.4s;
    animation-timing-function: linear;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        visibility: hidden;
    }
    to {
        opacity: 1;
        visibility: visible;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        visibility: visible;
    }
    to {
        opacity: 0;
        transform: translate(750px, -250px) scale(0.6);
    }
}