// Общие стили
.js-overlay {
  z-index: 1;
}

.modal-content {
  display: flex;
  margin: auto;
}

.modal-container {
  margin: auto;
  background-color: var(--modal-background-color);
  border-radius: 300px 0px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.round-picture img {
  margin: 32px auto 41px auto;
  border-radius: 50%;
}

.modal-item-name {
  color: var(--accent-color);
  list-style-type: none;
  font-weight: 600;
  font-size: 24px;
}

.modal-list {
  margin: -16px 0px -16px 0px;
}

.modal-item-text,
.price {
  color: var(--modal-text-color);
}

/////Кнопки и псевдо/////
.btn-buy {
  background-color: var(--modal-button-background);
  color: var(--sub-title-color);
  border-radius: 5px;
  height: 40px;
  width: 145px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.btn-more-from {
  border-radius: 5px;
  border: 1px;
  border-color: var(--modal-button-background);
  color: var(--modal-button-background);
  margin: 21px auto 32px auto;
  display: block;
  border-style: solid;
}
.btn-close {
  background: transparent;
  outline: none;
  border: 0;
  position: absolute;
  right: 30px;
  top: 30px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.ticket-icon {
  float: left;
  margin-right: 13px;
  margin-top: 2px;
  width: 29px;
  height: 19px;
}
.price-type-standart,
.price-type-vip {
  text-transform: capitalize;
}
.price-min-standart::after,
.price-min-vip::after {
  content: ' -';
}
.modal-item-text-price,
.modal-item-text {
  font-family: Montserrat;
  font-weight: normal;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

