//////////// Десктоп 1280+ ////////////
@media screen and (min-width: 1280px) {
  .modal-container {
    height: 953px;
    width: 930px;
    padding: 30px 30px 62px 30px;
  }
  .round-picture img {
    margin: 32px auto 41px auto;
    width: 132px;
    height: 132px;
  }
  .modal-image img {
    width: 360px;
    height: 601px;
  }
  .modal-content {
    height: 641px;
  }
  .modal-main-info {
    max-width: 450px;
    margin-left: 60px;
  }
  .modal-item-name {
    line-height: 29px;
  }
  .modal-item-name:not(:last-child) {
    margin-bottom: 20px;
  }
  .modal-item-text {
    font-size: 18px;
    line-height: 22px;
    margin-top: 7px;
  }
  .price {
    font-size: 18px;
    line-height: 22px;
    margin-top: 7px;
    margin-bottom: 20px;
  }
  .btn-buy {
    height: 40px;
    width: 145px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 24px;
  }
  .btn-more-from {
    width: 255px;
    height: 40px;
    font-size: 16px;
    line-height: 20px;
    width: 255px;
    height: 40px;
    margin: 15px auto 20px auto;
  }
}

//////////// Таблет 768-1279 ////////////
@media screen and (min-width: 768px) and (max-width: 1279.98px) {
  .modal-container {
    height: 831px;
    width: 595px;
  }
  .round-picture img {
    margin: 28px auto 43px auto;
    width: 70px;
    height: 70px;
  }
  .modal-image img {
    width: 233px;
    height: 416px;
  }
  .modal-content {
    height: 546px;
    width: 495px;
  }
  .modal-main-info {
    max-width: 265px;
    margin-left: 30px;
  }
  .modal-item-name {
    line-height: 24px;
  }
  .modal-item-name:not(:last-child) {
    margin-bottom: 10px;
  }
  .modal-item-text {
    font-size: 16px;
    line-height: 20px;
    margin-top: 5px;
  }
  .price {
    font-size: 16px;
    line-height: 20px;
    margin-top: 5px;
    margin-bottom: 10px;
    margin-left: 30px;
  }

  .btn-close {
    right: 25px;
    top: 25px;
    width: 17px;
    height: 17px;
  }

  .btn-more-from {
    width: 255px;
    height: 40px;
    font-size: 16px;
    line-height: 20px;
    width: 255px;
    height: 40px;
    margin: 43px auto 56px auto;
  }
  .ticket-icon {
    width: 24px;
    height: 16px;
    position: absolute;
    right: 34%;
  }
}

//////////// Мобилка 320-767.98 ////////////
@media screen and (max-width: 767.98px) and (min-width: 320px) {
  .modal-container {
    height: 815px;
    width: 272px;
    border-radius: 150px 0px;
  }
  .round-picture img {
    margin: 72px auto 25px auto;
    width: 102px;
    height: 102px;
  }
  .modal-image img {
    display: none;
  }
  .modal-main-info {
    max-width: 243px;
  }
  .modal-item-name {
    line-height: 24px;
    text-align: center;
  }
  .modal-item-name:not(:last-child) {
    margin-bottom: 10px;
  }
  .modal-item-text {
    font-size: 16px;
    line-height: 20px;
    margin-top: 5px;
  }
  .price {
    font-size: 16px;
    line-height: 20px;
    margin-top: 5px;
    margin-bottom: 10px;
    margin-left: 5px;
  }
  .btn-buy {
    margin-bottom: 15px;
  }
  .btn-close {
    right: 25px;
    top: 25px;
    width: 17px;
    height: 17px;
  }
  .btn-more-from {
    width: 255px;
    height: 40px;
    font-size: 16px;
    line-height: 20px;
    width: 255px;
    height: 40px;
    margin: 25px auto 104px auto;
  }
  .ticket-icon {
    width: 24px;
    height: 16px;
    position: absolute;
    right: 80%;
  }
}

