:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.container {
  display: flex;
  justify-content: center;
  margin: auto;
  padding: 0;

  @include mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @include mini {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

/* Стили выпадающего списка*/
form {
  width: 100%;
  position: relative;
  display: flex;
}

/* поиск в выпадающем списке*/
input[id='myInput'] {
  padding-left: 10px;
  background-color: #504e4e;
  min-width: 210px;

  height: 30px;
  color: hsl(0, 4%, 70%);
  font-size: 16px;
  border: none;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 20px;
  text-align: start;

  @include tablet {
    min-width: 218px;
    margin-left: 5px;
  }

  @include mobile {
    min-width: 218px;
    margin-left: 5px;
    background-color: rgb(95, 93, 93);
  }
}
input[id='myInput']::placeholder {
  color: var(--input-placeholder-footer);
}

.search-button__icon {
  position: relative;
  right: 38px;
  top: 15px;
  fill: var(--input-placeholder-footer);
  cursor: pointer;
}
.drop-button__icon {
  position: relative;
  right: 38px;
  fill: var(--input-placeholder-footer);
  cursor: pointer;
}

/* Стили инпута поиска  и кнопки выпадающего списка*/
.drop-btn,
.search-input-event {
  width: 270px;
  height: 40px;
  color: var(--input-placeholder-footer);
  border: none;
  border-radius: 3px;
  cursor: pointer;
  background: var(--input-bgr-color);
  backdrop-filter: blur(20px);
  border-radius: 20px;
  padding-left: 30px;
  padding-right: 0px;
  font-family: var(--main-font);
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-align: start;
  @include mini {
    width: 250px;
  }
}

.input-event {
  margin-right: 18px;
  margin-left: 10px;

  @include mobile {
    margin-bottom: 25px;
    margin-right: 0;
  }
  @include mini {
    margin-bottom: 20px;
    margin-right: 0;
  }
}

.search-input-event::placeholder {
  color: var(--input-placeholder-footer);
}

.dropdown-country {
  position: relative;
  left: 10px;
}

.dropdown-content {
  display: none;
}

.dropdown-content li {
  color: var(--input-placeholder-footer);
  padding: 15px 0 0 10px;
  text-decoration: none;
  display: block;
  cursor: pointer;
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 20px;
  text-align: start;
}

.show {
  display: block;
  position: absolute;
  left: 6%;
  border: none;
  cursor: pointer;
  background: var(--input-bgr-color);
  backdrop-filter: contrast(3) blur(20px);
  width: 238px;
  padding: 5px;
  height: 200px;
  overflow-y: auto;
  z-index: 1;

  @include mobile {
    background: var(--input-bgr-color);
    backdrop-filter: contrast(5) blur(230px);
    min-width: 230px;
    left: 6%;
  }

  @include mini {
    background: var(--input-bgr-color);
    backdrop-filter: contrast(5) blur(220px);
    max-width: 220px;
    left: 6%;
  }
}

.name-list {
  margin: 0;
  padding: 0;
}
